<script>
import Swiper from "swiper";
export default {
  data() {
    return {
      isShow: false,
      fisshow:false,
      sisshow:false,
      jisshow:false,
      n:0,
      m:0.3,
      b:null,
      datalist:{},
      idx:0
    };
  },
  methods:{
    fmyfn(){
      this.fisshow=!this.fisshow
    },
    smyfn(){
      this.sisshow=!this.sisshow
    },
    jmyfn(){
      this.jisshow=!this.jisshow
    }
  },
  created(){
    this.idx=this.$route.params.id
    this.$axios({
      url: `http://m.htche.com/sku/detail/show/${this.idx}`,
      method: "get"
    }).then(res => {
      this.datalist =
        res.data.message.conditionList[0].subValueSet[0].subValueSet[this.$route.params.i].productList[0];
        console.log(res.data.message.conditionList[0].subValueSet[0].subValueSet[this.$route.params.i].productList[0])
    });
  }

};
</script>

