<script>
import axios from "axios";

export default {
  data() {
    return {
      bid: 0,
      sid: 0,
      idData: []
    };
  },
  created() {
    this.bid = this.$route.params.bid;
    this.sid = this.$route.params.sid;
    axios
      .get(
        `http://m.htche.com/search/item/list?query=%5B%7B%22fieldType%22%3A%22BRANDID%22%2C%22value%22%3A%22${this.bid}%22%7D%2C%7B%22fieldType%22%3A%22SERIESID%22%2C%22value%22%3A%22${this.sid}%22%7D%5D&order=PRICE_ASC&page=1&rows=24`
      )
      .then(res => {
        this.idData = res.data.message.data;
        console.log(this.idData);
      });
    console.log(this.bid);
    console.log(this.sid);
  }
};
</script>

